<template>
  <mu-appbar>
    <mu-flat-button label="Blog" icon="home" slot="left" to="/" primary/>
    <mu-flat-button v-show="loginState" v-bind:label="name" slot="right"/>
    <mu-icon-menu icon="more_vert" slot="right">
      <mu-menu-item v-show="!name" title="" @click="login"/>
      <mu-menu-item v-show="!name" title="" @click="register" />
      <mu-menu-item v-show="name" title="" @click="addPost" />
      <mu-menu-item v-show="name" title="" @click="logout"/>
    </mu-icon-menu>
  </mu-appbar>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        title: 'blog',
        name: ''
      }
    },
    mounted: function () {
      if (this.$store.state.login) {
        this.$http.get(api.user).then((response) => {
          this.name = response.data.username
        }, (response) => {
          console.log(response)
        })
      }
    },
    methods: {
      'Home': function () {
        alert('home')
      },
      logout: function () {
        this.$store.commit('logout')
        this.$cookie.delete('token')
        localStorage.removeItem('login')
        this.$router.push({name: 'index'})
      },
      login: function () {
        this.$router.push({name: 'login'})
      },
      register: function () {
        this.$router.push({name: 'register'})
      },
      addPost: function () {
        this.$router.push({name: 'addPost'})
      }
    },
    computed: {
      loginState: function () {
        if (this.$store.state.login) {
          this.$http.get(api.user).then((response) => {
            this.name = response.data.username
          })
        } else {
          this.name = ''
        }
        return this.$store.state.login
      }
    }
  }
</script>



