<template>
  <div id="app">
    <moon-nav></moon-nav>
    <vue-particles
      color="#dedede"
      :particleOpacity="0.8"
      :particlesNumber="60"
      shapeType="circle"
      :particleSize="6"
      linesColor="#dedede"
      :linesWidth="1"
      :lineOpacity="0.4"
      :linesDistance="200"
      :moveSpeed="3"
      class="particles"
      style="position: fixed; background-size: cover;width: 100%; height: 100%;top:0;left:0;"
    >
    </vue-particles>
    <router-view></router-view>
    <moon-foot id="foot-bar"></moon-foot>
  </div>
</template>

<script>
import nav from './components/navbar.vue'
import foot from './components/footbar.vue'
export default {
  name: 'app',
  components: {
    'moon-nav': nav,
    'moon-foot': foot
  }
}
</script>
<style>
  .particles {
    position: absolute;
    background-size: cover;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
  }
</style>



