<template>
  <mu-flexbox justify="center" orient="vertical" style="margin: 8px;">
    <mu-flexbox-item class="register">
      <mu-card>
        <mu-text-field label="" hintext="32" icon="account_circle" type="text" :errorText="error_username" v-model="username" labelFloat/><br/>
        <mu-text-field label="" hintText="" icon="mail" type="email" :errorText="error_email" v-model="email"/><br/>
        <mu-text-field label="" hintText="8-20" icon="lock" type="password" :errorText="error_password" v-model="password" labelFloat/><br/>
        <mu-text-field label="" hintText="64" icon="place" type="text" v-model="location" labelFloat/><br/>
        <mu-text-field label="" icon="perm_contact_calendar" type="text" multiLine :rows="3" :rowsMax="6" v-model="about" labelFloat/><br/>
        <mu-card-actions>
          <mu-raised-button label="" v-on:click="register" primary/>
        </mu-card-actions>
      </mu-card>
    </mu-flexbox-item>
  </mu-flexbox>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        username: '',
        email: '',
        location: '',
        password: '',
        about: '',
        error_username: '',
        error_email: '',
        error_password: ''
      }
    },
    methods: {
      register: function () {
        let flag = true
        if (!this.username) {
          this.error_username = ''
          flag = false
        }
        if (!this.email) {
          this.error_email = ''
          flag = false
        }
        if (!this.password) {
          this.error_password = ''
          flag = false
        }
        if (!flag) return
        this.$http.post(
          api.user,
          {
            email: this.email,
            password: this.password,
            username: this.username,
            location: this.location,
            about: this.about
          }
        ).then((response) => {
          this.$cookie.set(
            'token',
            response.data.token,
            {expires: response.data.expiration + 's'}
          )
        }, (response) => {
          alert('fail')
        })
      }
    }
  }
</script>

<style scoped>
  .register {
    width: 50%;
    text-align: center;
  }
</style>



